.video-player {
  width: 100%;
  height: 450px;
  object-fit: cover;
  background: #000;
  border-radius: 20px;
}

/* Responsive video player */
@media (max-width: 768px) {
  .video-player {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .video-player {
    height: 250px;
  }
}

